<template>
    <div style="margin-top: 80px;">
        <mu-raised-button label="" @click="open"/>
        <mu-dialog :open="dialog" @close="close" title="" scrollable>
            <div class="column is-12">
                <label class="label"></label>
                <p class="control has-icon has-icon-right">
                    <mu-select-field v-model="game2" multiple>
                        <mu-menu-item value="1" title=""/>
                        <mu-menu-item value="2" title=""/>
                        <mu-menu-item value="3" title="HD"/>
                        <mu-menu-item value="4" title=""/>
                        <mu-menu-item value="5" title=""/>
                        <mu-menu-item value="6" title=""/>
                    </mu-select-field>
                </p>
            </div>
            <mu-select-field v-model="game3" label="">
                <mu-menu-item value="1" title=""/>
                <mu-menu-item value="2" title=""/>
                <mu-menu-item value="3" title="HD"/>
                <mu-menu-item value="4" title=""/>
                <mu-menu-item value="5" title=""/>
                <mu-menu-item value="6" title=""/>
            </mu-select-field>
            <div class="bottom">
                <mu-raised-button label="" @click="close" color="#fff" backgroundColor="#0184ce"/>
                <mu-raised-button label="" @click="close" color="#fff" backgroundColor="#23b989"/>
            </div>
        </mu-dialog>
    </div>
</template>
<script>
export default {
    name: 'resourceM',
    data: function () {
        return {
            dialog: false,
            game3: '',
            game2: ''
        }
    },
    methods: {
        open() {
            this.dialog = true
        },
        close() {
            this.dialog = false
        }
    }
}
</script>
<style>

</style>
    


